*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
.myProfile{
  /* background: gray; */
  display: flex;
  justify-content: center;
  margin-top: 10rem;
}
.profile img{
  width: 66px;
  height: 66px;
  object-fit: cover;
  border: 4px;
  border: 3px solid gray;
  cursor: pointer;
  border-radius: 50%;

}
.myProfile .profile .menu{
  position: relative;
  left: -13px;
  background: rgb(228, 220, 220);
  width: 142px;
  height: 160px;
  padding: 8px;
  margin-top: 5px;
  
  box-shadow:  0px 3px 6px rgba(0,0,0,0.1);
}

.myProfile .profile .menu li{
  list-style: none;
  column-gap: 10px;
  cursor: pointer;
  padding: 5px;
  
  
}
.myProfile .profile .menu li:hover{
  background-color: white;
}
